import React from 'react';

let PageTopAnchor = (props)=> {
  return(
    <div className="position-relative bg-dark translucent rounded">
      <a href="#page-container"><i className="fa-chevron-up fas fa-3x fa-fw text-white p-2"></i></a>
    </div>
  );
};

export default PageTopAnchor;


